.header {
  grid-area: header;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #ccc;
}

.user__info {
  display: flex;
  align-items: center;
  column-gap: 15px;
  height: 100%;
  padding: 15px;
}

.user__photo {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 50%;
}

.login {
  padding: 1rem;
}

.login__link {
  color: #333;
  text-decoration: none;
  font-size: 1.06rem;
  font-weight: 600;
}