.app-wrapper {
  display: grid;
  grid-template-areas: 
    "header header"
    "nav content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  width: 1200px;
  min-height: 100vh;
  padding:  0 15px;
  margin: 0 auto;
}

.content {
  grid-area: content;
  background-color: orange;
  max-width: 100%;
  padding: 1rem;
}