.nav {
    display: flex;
    flex-direction: column;
    grid-area: nav;
    background-color: #e5e5;
    padding: 1rem;
}
  
.nav__link {
    padding: 0.5rem 2rem;
    font-size: 1.06rem;
    color: #000;
    text-decoration: none;
    transition: color 0.2s linear;
}

.nav__link:hover {
    color: orange;
}

.nav__link.active {
    color: gold;
}